import Prop from './prop';
import ScriptPlay from '../cutscene/scriptplay';
import { DIRECTIONS } from '../define';

export default class WorkTable extends Prop {
    constructor(x, y, tileData) {
        super(x, y, tileData);

        this.isInteractive = true;
        this.upgraded = false;
    }

    upgrade() {
        //  
        this.tileTexture.texture = PIXI.Texture.fromFrame("house-worktable-upgrade.png");
        this.upgraded = true;
    }

    touch(game) {
        if (this.upgraded) {
            console.log("open worktable")
        } else {
            //   . 
            game.ui2.showConfirmModal(" ?", (confirmed) => {
                if (confirmed === "ok") {
                    this.upgrade();

                    //    .
                    //   
                    // TODO :     
                    const script = [
                        { 
                            command: "leavestage",
                            arguments: [] 
                        }, {
                            command: "enterstage",
                            arguments: ["house", { x: 10, y: 14, direction: DIRECTIONS.NW, margin: 2}],
                        }, {
                            command: "dialog",
                            arguments: [ {text:",  ", speaker: 1}]
                        }, {
                            command: "goto",
                            arguments: [2, 5],
                        }, {
                            command: "dialog",
                            arguments: [
                                {text: "!!!!!!", speaker: 1},
                                {text: "..  .", speaker: 1},
                                {text: "...", speaker: 1},
                                {text: " .. ?", speaker: 1}
                            ]
                        }
                    ]
                    game.playCutscene(script);
                }
            });
        }
    }
}



