import {doorIn} from "../cutscene/door";

//  
export default class Portal {
    constructor(game, from, to) {
        this.game = game;
        this.from = from;
        this.to = to;
        this.forceStop = true;
    }

    call() {
        //  
        //   .
        // TODO :     ...   
        //     

        // TODO :   !?!! 

        
        //    .
        this.game.playCutscene([
            { command: "leavestage", arguments: [this.from] },
            { command: "enterstage", arguments: [this.to.stage, this.to] },
        ]);
    }
}


